.page_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.image_logo {
  padding-top: 70px;
  width: 500px;
  height: 500px;
}

.section_rrss {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 5 0px 0 0 -10px;
}

.rrss__instagram,.rrss__facebook,.rrss__twitter,.rrss__mail {
  width: 30px;
  padding: 0 25px;
}

.rrss__instagram {
  padding-left: 0;
}

.rrss__mail {
  padding-top: 5px;
}
